<template>
  <div id="contact_form_subcomponent">
   
    <div class = "single_component">
        <label for="name"> {{info.label}}</label>
        <textarea v-bind:class="{error : displayError, success: formDeliveredPrompt}" type="text" v-bind:id="info.id" v-model="inputData"> </textarea>
        <p v-if="displayMessage" class="animated fadeInDown"> {{this.errorMessage[0]}} </p> 
    </div>
      
    
  </div>
</template>

<script>
import {bus} from '../../main.js'

export default {
  props: ['info'],
  data(){
      return{
         inputData : null,
         displayError : null,
         displayMessage: null, 
         formDeliveredPrompt : null,
         errorMessage : [],
      }
  },
  methods:{
  //all these validation functions first test whether the field and correct and then whether they are 
  //correctly completed

    checkName(){
     
      
         if(this.inputData){
           this.displayError = false;
           this.displayMessage = false;
           console.log(`this is ${this.info.id} component calling`)
           bus.$emit('gotName', this.inputData)
         }else{
           this.errorMessage.push(this.info.error);
           this.displayError = true;
           this.displayMessage = true;
           return undefined
         }
      
    },
    emailValidation() {
      const emailRegex = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}/g
      return emailRegex.test(this.inputData)
    },
    checkEmail(){
      
        if(this.inputData && this.emailValidation()){
          this.displayError = false;
          this.displayMessage = false;
          console.log(`this is ${this.info.id} component calling`)
          bus.$emit('gotEmail', this.inputData)
        }else{
          this.errorMessage.push(this.info.error);
          this.displayError = true
          this.displayMessage = true;
          return undefined
        }
      
    },
    
    checkText(){
      
        if(this.inputData){
          this.displayError = false;
          this.displayMessage = false;
          console.log(`this is ${this.info.id} component calling`)
           bus.$emit('gotText', this.inputData);
         }else{
           this.errorMessage.push(this.info.error);
           this.displayError = true;
           this.displayMessage = true;
           return undefined

         }
      
    },
    
    globalChecker(){
        if(this.info.id ==='name-input'){
        this.checkName();
      }else if(this.info.id === 'email-input'){
        this.checkEmail()
      }else{
        this.checkText()
      }
    }

  },
  
  created(){
    bus.$on('buttonPressed', ()=>{
    
      this.errorMessage = [];
      this.globalChecker();
    });
    bus.$on('formDelivered', ()=>{
      this.formDeliveredPrompt = true;
      
      if(this.info.id === 'message-input'){ 
        this.displayMessage = true;
        this.errorMessage.push(`Message sent successfully, I will contact you in 24 hours!`)
      }
    });
    bus.$on('formError', ()=>{
      this.displayError = true;
      this.displayMessage = true;
      if(this.info.id ==='message-input'){
      this.errorMessage.push(`Something went wrong, please use other methods that are shown in blue area below`)
      }
    })
  },
  beforeDestroy(){
    bus.$off('buttonPressed');
    bus.$off('formDelivered')
  }
  
}
</script>

<style scoped>

.single_component{
  font-family: 'Montserrat', 'sans serif';
  display:flex;
  flex-direction: column;
  width:100%;
}
label{
  
  font-size: 1.40625vw;
  font-weight: 100;
  margin : 1.0546875vw 0;
}
/* label{
  
  font-size: 2.25rem;
  font-weight: 100;
  margin : 1.6875rem 0;
} */
textarea{
  box-sizing:border-box;
  height: 3.0078125vw;
  width: 37.5vw;
  background-color: #EDEDF2;
  border: 1px solid #CCCCCE;
  border-radius : 6.351px;
  font-family: 'Montserrat', 'sans serif';
  padding: 0.875vw 1.25vw;
  font-size: 0.9375vw;
  font-weight:400;
  color: #434159;
  resize:none;
  overflow:hidden;

}
.error{
  border : 3px solid red;
}
.success{
  border : 3px solid green;
}
#message-input{
 
  height: 16.9375rem;


}
p{
  max-width: 65vw;
}
@media screen and (max-width: 1600px){
  textarea{
    height:4vw;
    font-size:1.5vw;
    padding-top: 1.2vw;

  }
}
@media screen and (max-width: 1240px){
  label{
    font-weight:400;
    font-size: 1.7vw;
  }
  textarea{
    width: 55vw;
    padding: 0;
    font-size:2vw;
    height: 6.6vw;
    padding: 2.2vw 2vw;
  }
}
@media screen and (max-width:767px){
  textarea{
    width: 65vw;
  }
  label{
    font-weight:400;
    font-size: 2.5vw;
  }
  textarea{
    padding: 0;
    font-size:3vw;
    height: 10vw;
    padding: 2.7vw 2.5vw;
  }
  .error{
  border : 2.5px solid red;
}
}
@media screen and (max-width: 460px){
  label{
    font-weight:400;
    opacity: .7;
    font-size: 4vw;
    
  }
  textarea{
    padding: 0;
    font-size:4vw;
    height: 10vw;
    padding: 2.5vw 2.5vw;
  }
  p{
    font-size:3.5vw;
  }
}

</style>
