<template>
  <div id="contact_form">
    <form>
      <contact-sub-component
      v-bind:info="componentInfo.nameComponent"></contact-sub-component>
      <contact-sub-component
      v-bind:info="componentInfo.emailComponent"></contact-sub-component>
      <contact-sub-component
      v-bind:info="componentInfo.messageComponent"></contact-sub-component>
    <contact-form-button></contact-form-button>
    </form>
    <img :src="illustration" id="illustration">
  </div>
</template>

<script>
import contact_sub_component from "./subComponents/contact_form_component.vue"
import contact_form_button from "./subComponents/contact-form-button.vue"
import {bus} from '../main.js'
import axios from 'axios'

export default {
  components: {
    "contact-sub-component" : contact_sub_component,
    "contact-form-button" : contact_form_button,
  },
  data(){
    return{
      formDetails:{
        name: null,
        email : null,
        message : null,  
      },
      count : 0,
      componentInfo:{
        nameComponent: {
          label : 'Name',
          id : 'name-input',
          error: 'Please enter Your name'
        },
        emailComponent: {
          label : 'E-mail',
          id: 'email-input',
          error: 'The E-mail you have entered is invalid'
        },
        messageComponent: {
          label : 'Message',
          id: 'message-input',
          error: 'This field cannot be empty',
        },
      },
      illustration : require('C:/Users/Marijus/Desktop/Portfolio page/portfolio_page/assets/form_illustration.svg')
    }
  },
  methods: {
    sendMessageRequest(){
      if(this.count === 3){
        axios.post('https://fast-dawn-99682.herokuapp.com/send', this.formDetails)
        .then(
          (res) => {
             if(res.status == '200'){
               bus.$emit('formDelivered');
             }else{
               bus.$emit('formError')
             }
             this.count=  0})
             .catch(err => console.log(err));
      }
    }
  },
  created(){
    bus.$on('gotName', (data)=>{
      this.formDetails.name = data;
      this.count++
      console.log(this.formDetails.name,this.formDetails.count);

    });
    bus.$on('gotEmail', (data)=>{
      this.formDetails.email = data;
      this.count++
      console.log(this.formDetails.email,this.formDetails.count);

    });
    bus.$on('gotText', (data)=>{
      this.formDetails.message = data;
      this.count++
      console.log(this.formDetails.message,this.formDetails.count);
      this.sendMessageRequest();
    });

  },
  beforeDestroy(){
    bus.$off('gotName');
    bus.$off('gotEmail');
    bus.$off('gotText');
  }
}
</script>

<style scoped>
#contact_form{
  display:flex;
  margin-bottom: 18.125rem;
  justify-content: space-between;
}

form{ 
  max-width:37.0530078125vw;
  margin-left:8.6328125vw;
  display:flex;
  flex-direction: column;
}
#illustration{
  margin-left: 2rem;
  margin-top: 1.0546875vw;
  max-height: 33.8671875vw;
  margin-right: 8.2390625vw;
}
@media screen and (max-width:1240px){
  
  #illustration{
    display:none;
  }
  #contact_form{
    justify-content:center;
  }
  form{
    margin-left: 0;
    max-width:80vw;
  }
}
</style>
