<template>
  <div id="content_box" v-bind:style= "{marginBottom : lastComponentMargin + 'vw'}" >
      <div id="container" v-bind:style= "{marginBottom : lastComponentTextMargin + 'vw'}">
        <div id="content_wrapper">
            
                <p v-bind:id="info.id" class="content_number" v-bind:style = "{right: webWordAdjustment + 'vw'}">
                    {{info.number}}
                </p>
                
                <div id="content_text" v-bind:style = "{marginLeft: textMarginLeft + 'vw'}">
                    <p v-bind:id="info.headerID" class="content_text_header">
                    {{info.header}} 
                    </p>
                    <div>
                        <p  class="content_text_line">{{info.text}}</p>
                        
                    </div>
                </div>
        </div>
        <div id="content_image">
            <img v-bind:id="info.imageID" :src="info.illustration">
            </div>
    </div>
  </div>
</template>

<script>


export default {
    props: ['info'],
  data() {
    return{
       isService : false,
       analysis :false,
      }
    },
 
  computed : {
      textMarginLeft(){
          
            if(this.info.number==="Web"){
                return 5 //since web-word has different spacing than numbers
            }else if(this.info.number==='3' )
                {return  1
                }},
    webWordAdjustment(){
        if(this.info.header ==='Upkeep' && window.innerWidth<767){
            if(window.innerWidth<461){
                return 75.7
            }
            else if(window.innerWidth<768){
                return 72}
        }
    },
    lastComponentMargin(){
        if(this.info.header === 'Upkeep'){
            return 0
        }
    },
    lastComponentTextMargin(){
        if(this.info.header === 'Upkeep'){
            return 7.8125
        }
    },
      },
   

  }  

    
  

</script>

