<template>
  <div id="nav">
    <div id="navigation_wrapper">
      <div id="logo" >
        <img class="logotipas" :src="images.logo">
      </div>

      <div id="navigation">
        <nav id="nav-wrapper">
          
          <ul v-bind:class="{ fadeInRightBig : fadeInRightBig, 
                              fadeOutRight : fadeOutRight}" 
                              class="navigation-links animated fast">
            <li v-for="link in navigation" :key="link.id">
              <a v-bind:href="`#${link[1]}`" class="link" @click="closeNav" >{{link[0]}}</a></li>
            
          </ul>
          <div id="nav-image" @click="expandNav" >
            <img :src="images.navigation">
          </div>
        </nav>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  data() {
    return{
      images : {
        logo : require('C:/Users/Marijus/Desktop/Portfolio page/portfolio_page/assets/logo.svg'),
        navigation : require('C:/Users/Marijus/Desktop/Portfolio page/portfolio_page/assets/nav_illustration.svg')
      },
      navigation : [['Home', 'hero-section'], ['Strategy', 'first__block'], ['Services', 'second__block'],
      ['Contact Form', 'sub_hero_main_text_contact_me']],
      
      fadeInRightBig : null,
      fadeOutRight: null,
      blur : false,
    }
  },
  methods: {
    expandNav () {
     if(this.fadeInRightBig || this.fadeOutRight){
       
      this.fadeInRightBig = !this.fadeInRightBig
      this.fadeOutRight= !this.fadeOutRight}else{
        this.fadeInRightBig = true;
        this.fadeOutRight =false;

      }
    },
    closeNav(){
      this.fadeInRightBig = !this.fadeInRightBig
    }
   

  }
}
</script>

<style scoped>
#nav{
    width:100%;
    margin: 0px;
    padding : 0px;
    min-height: 5rem;
    background-color: #4841AA;
    display:flex;
    justify-content:center;
    
    
}
#navigation_wrapper{
  margin-top: .5vw;
    display:flex;
    justify-content:space-between;
    align-items:center;
    width: 100%;
}

#nav-wrapper{
  display:flex;
  
  justify-content: flex-end;
}
#logo{
  margin-left:7.5vw;
  min-height:1.875vw;
}
.logotipas{
  height:1.875vw;
  min-height: 36px;
}
ul{
  min-height: 3rem;
  height: 5rem;
  display:flex;
  align-items:center;
  padding: 0px;
  margin-right: 7.5vw;
  text-decoration: none;
 
 
}
ul li{
  list-style:none;
  margin-left: 4.453125vw;
}
a{
  color:#ffffff;
  text-decoration:none;
  font-family: 'Montserrat' , "sans serif";
  font-weight: 100;
}
.navigation-links{
  /* font-size: 1.015625vw; */
  font-size: 1.015625vw;
}
#nav-image{
  display:none;
  margin-right:7.5vw;
}

@media screen and (max-width:1500px){
  .link{
    font-weight: 400;
    opacity: .8
  }
}
@media screen and (max-width:1240px){
  .navigation-links{
    display:none;
  }
  .fadeInRightBig{
    display:flex;
    margin-right:6vw;
    font-size: 1.5vw;
  }
  .fadeOutRight{
    
    display:flex;
    margin-right:2vw;
    font-size: 1.5vw;
  }
  #nav-image{
    display:flex;
    max-width: 50px;
    z-index: 1;
    align-items:center;
  }
  #nav-image img{
    min-height: 3rem;
    height: 5rem;
    width: 45px;
    cursor: pointer;
  }
}
@media screen and (max-width: 767px){
  ul{
    display:flex;
    flex-direction: column;
    position:absolute;
    text-align:center;
    background-color:#4841AA;
    margin-top: 20vw;
    height:100vh;
    width:100%;
    max-width:100% !important;
    overflow-x:hidden !important;
  
  }
  .fadeInRightBig{
    margin-right:0px;
    margin-left: 0px;
  }
  
  ul li{
    align-self:center;
    z-index:1;
    font-size: 5vw;
    margin-top:5vw;
    text-align:left;
    margin-right:1vw;
  }
  .blur{
    filter:blur(10px);
  }
}
</style>
